.c3-trace-card {
  --c3-trace-card-border-color: rgba(0, 0, 0, 0.12);
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: start;
  font-size: 14px;
}
.c3-trace-card.align-right {
  flex-direction: row-reverse;
}
.c3-trace-card c3-trace-card-title {
  padding: 0.5rem 1rem;
  font-size: 1.5rem;
  line-height: 2rem;
  border: var(--c3-trace-card-border-color) solid 1px;
  border-radius: 1rem;
  box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2), 0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 1px 3px 0px rgba(0, 0, 0, 0.12);
}
.c3-trace-card .content {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}
.c3-trace-card .content .horizontal-line {
  margin-top: 25px;
  height: 1px;
  background-color: var(--c3-trace-card-border-color);
}
